import React from 'react';
import 'css-doodle';

export default function Doodle(props) {
  const { text } = props;
  let doodle = null;
  if (/|fish/i.test(text)) {
    doodle = 'fish';
  } else if (/||candy||Christmas|xmas|/i.test(text)) {
    doodle = 'candy'
  }

  if (!doodle) { return null; }

  return (
    <css-doodle use="var(--rule)" class={doodle}></css-doodle>
  )
}
